import {isDevMode} from '@angular/core';


/* eslint-disable-next-line @typescript-eslint/no-explicit-any */
export function adaptLog(...data: any[]): void {
  if (isDevMode()) {
    // eslint-disable-next-line no-console
    console.log(...data);
  }
}

/* eslint-disable-next-line @typescript-eslint/no-explicit-any */
export function adaptWarn(...data: any[]): void {
  if (isDevMode()) {
    // eslint-disable-next-line no-console
    console.warn(...data);
  }
}

/* eslint-disable-next-line @typescript-eslint/no-explicit-any */
export function adaptError(...data: any[]): void {
  if (isDevMode()) {
    // eslint-disable-next-line no-console
    console.error(...data);
  }
}
